




















import CardComponent, { ICard, suit } from '../components/Card.vue';
import StatusVisualizer from '../components/StatusVisualizer.vue';
import Component from 'vue-class-component';
import Vue from 'vue';
import { GameCore } from '../../common/models/game-core';
import { Prop, Emit } from 'vue-property-decorator';
import { keyUp } from '@common/utils/sequence';
import { Card } from '@common/models/card';

@Component({
    components: {
        CardComponent,
        StatusVisualizer,
    },
})
export default class PinTable extends Vue {
    @Prop()
    private gameCore!: GameCore;

    @Prop({ default: false })
    private highlightCentral!: boolean;

    @Prop({ default: false })
    private highlightBack!: boolean;

    private keyUp = keyUp;

    @Emit('newGame')
    private newGame() { }

    private get pinRows() {
        return keyUp(this.gameCore.pinTable.cardRows);
    }

    private pinClick(card: Card) {
        if (!this.gameCore.isGameOver) {
            this.gameCore.select(card);
        }
    }


}
