





import { Component, Vue, Prop } from 'vue-property-decorator';
@Component({})
export default class Hamburger extends Vue {
  private readonly size = 15;

  private get lines() {
    const ls = [];
    for (let i = 0; i < 3; i++) {
      ls.push({ id: i, top: 8 + 2 * i * this.size + '%' });
    }
    return ls;
  }
}
